import React from 'react';
import './Member.css';

const Member=(props)=>(
    <div className='col-6 col-md-3'>
        <div className='member'>
            <div className='memberBgImage' style={{backgroundPosition:'center', backgroundImage:`url(${props.memberImg})`}} onClick={()=> window.open(`${props.memberImg}`, "_blank")}></div>
            <h6>{props.name}</h6>
            <p>Rank:{props.rank}</p>
            <p>{props.text}</p>
        </div>
    </div>
)

export default Member;